.input-radio .input-wrapper,
.input-radio-checked .input-wrapper,
.input-radio-tab .input-wrapper, .input-checkbox .input-wrapper {
  display: flex;
  align-items: center;
  min-height: 1.375rem;
}
.input-radio label,
.input-radio-checked label,
.input-radio-tab label, .input-checkbox label {
  position: relative;
  padding-left: 35px;
  margin: 0;
  color: #4d535b;
  font-weight: 500;
  cursor: pointer;
}
.input-radio label::before,
.input-radio-checked label::before,
.input-radio-tab label::before, .input-checkbox label::before {
  content: "";
  position: absolute;
  z-index: 1;
  width: 10px;
  height: 5px;
  transition: transform 0.4s;
}
.input-radio label::after,
.input-radio-checked label::after,
.input-radio-tab label::after, .input-checkbox label::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  background: #ffffff;
  border: 2px solid #00A8AD;
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  transition-delay: 0s;
}
@media (prefers-reduced-motion: reduce) {
  .input-radio label::after,
.input-radio-checked label::after,
.input-radio-tab label::after, .input-checkbox label::after {
    transition: none;
  }
}
.input-radio input[type=radio],
.input-radio-checked input[type=radio],
.input-radio-tab input[type=radio], .input-checkbox input[type=radio],
.input-radio input[type=checkbox],
.input-radio-checked input[type=checkbox],
.input-radio-tab input[type=checkbox],
.input-checkbox input[type=checkbox] {
  display: none;
  opacity: 0;
}
.input-radio input[type=radio][disabled] + label,
.input-radio-checked input[type=radio][disabled] + label,
.input-radio-tab input[type=radio][disabled] + label, .input-checkbox input[type=radio][disabled] + label,
.input-radio input[type=checkbox][disabled] + label,
.input-radio-checked input[type=checkbox][disabled] + label,
.input-radio-tab input[type=checkbox][disabled] + label,
.input-checkbox input[type=checkbox][disabled] + label {
  cursor: not-allowed;
  color: #4d535b;
}
.input-radio input[type=radio][disabled] + label::after,
.input-radio-checked input[type=radio][disabled] + label::after,
.input-radio-tab input[type=radio][disabled] + label::after, .input-checkbox input[type=radio][disabled] + label::after,
.input-radio input[type=checkbox][disabled] + label::after,
.input-radio-checked input[type=checkbox][disabled] + label::after,
.input-radio-tab input[type=checkbox][disabled] + label::after,
.input-checkbox input[type=checkbox][disabled] + label::after {
  border-color: #4d535b;
}

